import React, { Component, Fragment } from "react";
import Axios from "axios";
import Tbody from "../components/tbody";
import Tbody2 from "../components/tbody2";
import Tbody3 from "../components/tbody3";

class WeatherAPI extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: {},
      data2: {},
      data3: {}
    };
  }
  componentDidMount() {
    // DỮ LIỆU THÁNG 9/2018
    Axios({
      method: "GET",
      url:
        "https://api.worldweatheronline.com/premium/v1/past-weather.ashx?key=bb11fe1292f747d5a6584956192509&q=Gia+Lai&date=2018-09-01&enddate=2018-09-30&tp=3&format=json&includelocation=yes&showlocaltime=yes"
    })
      .then(result => {
        this.setState({ data: result.data });
      })
      .catch(err => console.log(err));

    // DỮ LIỆU THÁNG 10/2018
    Axios({
      method: "GET",
      url:
        "https://api.worldweatheronline.com/premium/v1/past-weather.ashx?key=bb11fe1292f747d5a6584956192509&q=Gia+Lai&date=2019-08-01&enddate=2019-08-31&tp=6&format=json&includelocation=yes&showlocaltime=yes"
    })
      .then(result => {
        this.setState({ data2: result.data });
      })
      .catch(err => console.log(err));

    // DỮ LIỆU THÁNG 6
    Axios({
      method: "GET",
      url:
        "https://api.worldweatheronline.com/premium/v1/past-weather.ashx?key=bb11fe1292f747d5a6584956192509&q=Gia+Lai&date=2018-09-01&enddate=2018-09-30&tp=3&format=json&includelocation=yes&showlocaltime=yes"
    })
      .then(result => {
        this.setState({ data3: result.data });
      })
      .catch(err => console.log(err));
  }





  // DỮ LIỆU THÁNG 9/2018
  renderWeatherApi = () => {
    const { data } = this.state;

    // this.state.data.data.weather.map((item, index) => {
    //   return item;
    // });
    return data.data
      ? data.data.weather.map((item, index) => {
          return <Tbody item={item} key={index} />;
        })
      : "";
  };

   // DỮ LIỆU THÁNG 10/2018
  renderWeatherApi2 = () => {
    const { data2 } = this.state;

    // this.state.data.data.weather.map((item, index) => {
    //   return item;
    // });
    return data2.data
      ? data2.data.weather.map((item, index) => {
          return <Tbody item={item} key={index} />;
        })
      : "";
  };

  // DỮ LIỆU THÁNG 6
  renderWeatherApi3 = () => {
    const { data3 } = this.state;

    // this.state.data.data.weather.map((item, index) => {
    //   return item;
    // });
    return data3.data
      ? data3.data.weather.map((item, index) => {
          return <Tbody3 item={item} key={index} />;
        })
      : "";
  };

 
  

  render() {
    console.log(this.state.data.data);
    console.log(this.state.data3.data);
    return (
      <Fragment>
        <div>
          <h1
            style={{
              fontSize: "4rem",
              fontWeight: "bold",
              margin: "auto",
              color: "green"
            }}
          >
            GIA LAI HISTORICAL WEATHER DATA TABLE
          </h1>
        </div>
        <table class="table" style={{ fontSize: "5rem" }}>
          <thead style={{ fontSize: "33px", background:'green', color:'white' }}>
            <tr>
              <th
                style={{ borderRight: "1px solid green", width: "100px" }}
                scope="col"
              >
                Year/Month/Day
              </th>
              <th scope="col">0:00AM(Temperature)</th>
              <th scope="col">0:00AM(Humidity)</th>
              <th style={{borderRight:'1px solid green'}} scope="col">0:00AM(Wind)</th>
              <th scope="col">3:00AM(Temperature)</th>
              <th scope="col">3:00AM(Humidity)</th>
              <th style={{borderRight:'1px solid blue'}} scope="col">3:00AM(Wind)</th>
              <th scope="col">6:00AM(Temperature)</th>
              <th scope="col">6:00AM(Humidity)</th>
              <th style={{borderRight:'1px solid red'}} scope="col">6:00AM(Wind)</th>
              <th scope="col">9:00AM(Temperature)</th>
              <th scope="col">9:00AM(Humidity)</th>
              <th style={{borderRight:'1px solid green'}} scope="col">9:00AM(Wind)</th>
              <th scope="col">12:00AM(Temperature)</th>
              <th scope="col">12:00AM(Humidity)</th>
              <th style={{borderRight:'1px solid blue'}} scope="col">12:00AM(Wind)</th>
              <th scope="col">15:00AM(Temperature)</th>
              <th scope="col">15:00AM(Humidity)</th>
              <th style={{borderRight:'1px solid red'}} scope="col">15:00AM(Wind)</th>
              <th scope="col">18:00AM(Temperature)</th>
              <th scope="col">18:00AM(Humidity)</th>
              <th style={{borderRight:'1px solid green'}} scope="col">18:00AM(Wind)</th>
              <th scope="col">21:00AM(Temperature)</th>
              <th scope="col">21:00AM(Humidity)</th>
              <th style={{borderRight:'1px solid blue'}} scope="col">21:00AM(Wind)</th>
            </tr>
          </thead>
          <tbody>
            {this.renderWeatherApi()}
            <br />
            {this.renderWeatherApi2()}
            {/* {this.renderWeatherApi3()} */}
            <br />
            
          </tbody>
        </table>
      </Fragment>
    );
  }
}

export default WeatherAPI;
